.header {
  margin-bottom: 25px;
  &__top {
    background-color: $header-color;
    border-bottom: 3px solid darken(#6cc3f8, 30%);
  }

  &__inner {
    position: relative;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
  }

  &__logo {
    margin-right: 20px;
  }

  &__social {
    display: flex;
    margin-right: 3.6vw;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__icon {
    width: 25px;
    height: 25px;
    fill: #fff;
    transition: transform 0.2s;
    &:hover {
      transform: scale(1.5,1.5);
    }
    
  }

  &__profile {
    background-color: #6cc3f8;
    width: 40px;
    color: inherit;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__content {
    background-color: #fff;
    border-bottom: 3px solid darken(#6cc3f8, 30%);
  }

  &__text {
    padding: 10px 0;
    min-height: 120px;
    display: flex;
    align-items: center;
  }

  &__title {
    color: $title-color;
    font-family: Raleway, sans-serif;
    font-size: 36px;
  }

  &__subtitle, &__descr {
    font-size: 24px;
    color: $main-color;
    font-weight: 600;
  }

  &__descr {
    font-size: 18px;
  }
}