.news {
  &__wrapper {
    display: flex;
  }

  &__info {
    max-width: 100px;
    background-color: #f3f7fa;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 14px;
  }

  &__item {
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }

  &__icon {
    width: 20px;
    height: 20px;
    background-color: gold;
  }

  &__img {
    max-width: 200px;
    width: 100%;
    height: 200px;
    background-color: gold;
    margin-right: 15px;
  }

  &__inner {
    display: flex;
  }

  &__text {
    p {
      line-height: 1.4;
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
  }


}